.styletube{

    border: 40px solid #350455;
    
    width:650px;
}


@media(min-width:1920px){

    .styletube{
        
        height: 500px;
        width:750px;
    }
    
}
@media(max-width:1100px){

    .styletube{
        
        height: 400px;
        width:650px;
    }
    
}

@media(max-width:900px){
    
    .styletube{
        
      
        width:500px;
    }
    
 }
 
 @media(max-width:700px){
    
    .styletube{
        
      
        width:400px;
    }
    
 }